import React from "react";

import { Html } from "@mui/icons-material";
let contents = [
  {
  heading: "-   ",
  content: " -             HTML, CSS, Bootstrap    "
},
{
  heading: "   ",
  content: "         REACT            ,                   "
},{
  heading: "-   ",
  content: " -             HTML, CSS, Bootstrap    "
},
{
  heading: "   ",
  content: "         REACT            ,                   "
},{
  heading: "-   ",
  content: " -             HTML, CSS, Bootstrap    "
},
{
  heading: "   ",
  content: "         REACT            ,                   "
}

]

const courseEndPartContents = [
  {
    heading: "   ",
    content: " -                            "
},
{
  heading: "   ",
  content: " -                            "
}
]
const CourseDescription = () => {
  return (
    <>
    <div className="course__description">
      {contents.map(c=> {
        return(
          <>
        <div>
        <h3>{c.heading}</h3>
      </div>
      <div className="gutter__div"></div>

      <hr />
    <div className="gutter__div"></div>

      <div>
        <p>
         {c.content}
        </p>
      </div>
    <div className="gutter__div"></div>

          </>
        )
      })}
      
    </div>
    {/* <div className="gutter__div"></div> */}
    

    {/********************************************** end part *******************************************************/}

<div>
  {courseEndPartContents.map(c=> {
    return (
      <>
      <div className="course__end__part">
            <div className="course__end__part__left">
              <div className="card__img">
                <Html
                  color="white"
                  sx={{
                    backgroundColor: "#a7174e",
                  }}
                />
              </div>
            </div>
            <div className="course__end__part__right">
            <h3>{c.heading}</h3>
            <div className="gutter__div"></div>
            <p>
             {c.content}
            </p>
            </div>
      </div>
      <div className="gutter__div"></div>
      
      </>
    )
  })}
    
</div>
  </>
  )
}

export default CourseDescription